import { useEffect } from 'react'
import  {withTheme} from '../../paper/core/theming'
export const Styles = withTheme(({ theme }) => {
  
    useEffect(() => {
        let stylesheet = document.getElementById('datetime-stylesheet')
        if(stylesheet === null){
            stylesheet = document.createElement('style')
            document.head.append(stylesheet)
            stylesheet.innerText = styles(theme.colors.primary)
        }
    }, [])
    return null
})

let styles =  color => `
.m-calendar {
    display: inline-block;
  }
  .m-calendar table {
    width: 100%;
    border-collapse: collapse;
    border-spacing: 0;
    table-layout: fixed;
  }
  .m-calendar td {
    padding: 8px 0;
    text-align: center;
    cursor: pointer;
    color: #dfe0e4;
    border: 1px solid #dfe0e4;
  }
  .m-calendar thead td {
    color: ${color};
    font-size: 11px;
    font-weight: 700;
    text-transform: uppercase;
    font-size: 12px;
  }
  .m-calendar tbody td {
    color: #666666;
  }
  .m-calendar tbody td:hover {
    background: ${color};
    border-color: ${color};
    color: #ffffff;
  }
  .m-calendar .current-day {
    color: ${color};
    font-weight: bold;
  }
  .m-calendar .prev-month,
  .m-calendar .next-month {
    color: #999999;
  }
  .m-calendar .toolbar {
    line-height: 30px;
    color: ${color};
    text-align: center;
    margin-bottom: 13px;
  }
  .m-calendar .toolbar button {
    position: relative;
    width: 30px;
    height: 30px;
    line-height: 30px;
    color: #ffffff;
    border: 1px solid ${color};
    border-radius: 50%;
    background: ${color};
    font-size: 20px;
    padding: 0;
    text-align: center;
    outline: 0;
    z-index: 5;
    cursor: pointer;
  }
  .m-calendar .toolbar .prev-month {
    float: left;
  }
  .m-calendar .toolbar .next-month {
    float: right;
  }
  .m-calendar .toolbar .current-date {
    color: ${color};
  }
  .m-time {
    color: #ffffff;
  }
  .m-time .showtime {
    text-align: center;
    padding-top: 30px;
  }
  .m-time .separater {
    display: inline-block;
    font-size: 32px;
    font-weight: bold;
    color: ${color};
    width: 32px;
    height: 65px;
    line-height: 65px;
    text-align: center;
  }
  .m-time .time-text {
    position: relative;
    left: -10px;
    font-size: 15px;
    color: ${color};
    margin-top: 7px;
    margin-bottom: 10px;
  }
  .m-time .sliders {
    margin-top: 30px;
    padding: 0 10px;
  }
  .m-time .time {
    width: 65px;
    height: 65px;
    display: inline-block;
    font-size: 38px;
    line-height: 65px;
    background-color: ${color};
    border-radius: 3px;
    text-align: center;
  }
  .u-slider-time {
    position: relative;
    display: inline-block;
    background-color: #dfe0e4;
    border-radius: 3px;
    height: 4px;
    width: 100%!important;
    cursor: pointer;
  }
  .u-slider-time .value {
    position: absolute;
    background-color: ${color};
    border-radius: 3px;
    top: 0;
    height: 100%;
  }
  .u-slider-time .handle {
    position: absolute;
    width: 4px;
    height: 4px;
  }
  .u-slider-time .handle:after {
    position: relative;
    display: block;
    content: '';
    top: -10px;
    left: -12px;
    width: 24px;
    height: 24px;
    background-color: #fff;
    border: 3px solid ${color};
    border-radius: 50%;
    cursor: pointer;
  }
  .im-btn {
    display: inline-block;
    background-color: #ffffff;
    border: 0;
    outline: 0;
    cursor: pointer;
    line-height: 1;
  }
  .im-btn:before {
    margin-right: 6px;
  }
  .m-input-moment {
    display: inline-block;
    width: 330px;
    padding: 12px 15px;
  }
  .m-input-moment .options {
    width: 100%;
    display: inline-block;
    margin-bottom: 4px;
  }
  .m-input-moment .options button {
    float: left;
    width: 50%;
    color: ${color};
    text-align: center;
    font-size: 16px;
    padding: 7px;
    border: 1px solid ${color};
    border-radius: 3px;
  }
  .m-input-moment .options button:first-child {
    border-top-right-radius: 0;
    border-bottom-right-radius: 0;
  }
  .m-input-moment .options button:last-child {
    border-top-left-radius: 0;
    border-bottom-left-radius: 0;
  }
  .m-input-moment .options button.is-active {
    color: #ffffff;
    background-color: ${color};
  }
  .m-input-moment .tab {
    display: none;
    height: 275px;
  }
  .m-input-moment .tab.is-active {
    display: block;
  }
  .m-input-moment .tabs {
    margin-bottom: 11px;
  }
  .m-input-moment .btn-save {
    display: block;
    margin-top: 10px;
    width: 100%;
    background-color: ${color};
    padding: 12px 0;
    text-align: center;
    color: #ffffff;
    font-size: 16px;
    border-radius: 3px;
  }  
`